.loader {
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: 1s linear infinite rotation;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  background: #ff3d00;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(150%, 150%);
}

.loader:before {
  inset: auto 0 0 auto;
  transform: translate(-150%, -150%);
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

.cat-info {
  flex-direction: row;
  justify-content: start;
  align-items: flex-start;
  gap: 50px;
  margin-top: 30px;
  display: flex;
}

.box {
  max-width: 400px;
}

.cat-size {
  background-size: cover;
  width: 530px;
  height: 350px;
}

